
import { computed } from "vue";
export default {
  name: "footer",
  props: {
    todos: {
      type: Array,
      required: true,
    },
    dela: {
      type: Function,
      required: true,
    },
    checknum: {
      type: Number,
      required: true,
    },
    len: {
      type: Number,
      required: true,
    },
    updateCheck: {
      type: Function,
      required: true,
    },
    checkItem:{
      type:Number,
      required:true
    }
  },
  setup(props) {
    const dellist = () => {
      
      if (window.confirm("确定删除所有完成事项")) {
        props.dela();
      }
    };
    const selectall = computed({
      get(){
        return props.checkItem===props.len && props.len !== 0? true:false
      },
      set(val){
        for(let i = 0; i < props.len; i++){
          props.updateCheck(props.todos[i],val)
        }
      }
    })
    return {
      dellist,
      selectall
    };
  },
};
