
import { ref } from 'vue'
  export default {
      name: 'header',
      props:{
        addTodo:{
          type:Function,
          required:true
        }
      },
      setup(props){
        const text = ref('')
        const add = ()=>{
          const titletext = text.value;
          if(!titletext.trim()) return
          const todo = {
            id: Date.now(),
            title:titletext,
            ischecked:false
          }
          props.addTodo(todo)
          text.value = ''
        }
        return {
          add,
          text
        }
      }
  }
